<template lang="pug">
    .content-note
        .group
            .header-content
                .user
                    .photo
                    .content-user
                        h3.name {{this.currentNote.user.master ? 'Você' : this.currentNote.user.name}}
                        span.date {{this.currentNote.date}}

                .wrapper-options(ref="options")
                    button.button-options(@click="toggleOptions") Opções
                    button.button-back(@click="toggleOptions")
                    
                    .buttons
                        button.button-hashtag Hashtags
                        button.button-arquivos Aquivos
                        button.button-lembrete Lembretes

                        .more
                            button.button-more Mais Opções

                            .buttons-more
                                button.button-item Item
            .content-text
                .content-title
                    we-editable(
                        class="title",
                        :content="this.currentNote.title",
                        @update="this.currentNote.title = $event"
                    )

                    .buttons
                        button.button-add Salvar
                        button.button-cancel Cancelar
                    
                .content-description
                    we-editable(
                        class="description",
                        :content="this.currentNote.description",
                        @update="this.currentNote.description = $event"
                    )

                    .buttons
                        button.button-add Salvar
                        button.button-cancel Cancelar

        .footer
            button.button-convert-task Converter em tarefa

</template>

<script>
    import Notes from '@/components/notes/notes.js'
    import WeEditable from '@/components/common/We-Editable.vue'

    export default {
        name: 'we-contentNote',
        components:{
            'we-editable': WeEditable
        },
        data () {
            return {
                notes: Notes.notes,
                currentNote: null,
                msg: 'Welcome to Your Vue.js App'
            }
        },
        created(){
            this.getNote();
        },
        mounted(){
            console.log(this.notes);
        },
        methods: {
            getNote(){
                let idNote = this.$route.params.idNote;

                for (var i = 0; i < Notes.notes.length; i++) {
                    if (Notes.notes[i].id == idNote) {
                        this.currentNote = Notes.notes[i];
                    }
                }
            },
            toggleOptions(){
                this.$refs.options.classList.toggle('show-options');
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/common/modais/ContentNote.styl"></style>


