<template lang="pug">
    .modal
        .overlay(@click="close")
        .wrapper-modal
            we-content-task(v-if="isTask")
            we-content-note(v-if="isNote")
</template>

<script>
    import WeContentNote from '@/components/common/modais/We-ContentNote.vue';
    import WeContentTask from '@/components/common/modais/We-ContentTask.vue';

    export default {
        name: 'we-modal',
        components:{
            'we-content-task': WeContentTask,
            'we-content-note': WeContentNote
        },
        data () {
            return {
                isTask: false,
                isNote: false,
                msg: 'Welcome to Your Vue.js App'
            }
        },
        created(){
            if (this.$route.params.type === 'task') {
                this.isTask = true;
            }else{
                this.isNote = true;
            }
        },
        moutend(){
            console.log('oi');
        },
        methods: {
            close(){
                this.$router.go(-1)
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/modais/Modal.styl"></style>


