<template lang="pug">
	draggable(
		class="content-board",
		v-model="currentCols",
		@start="drag=true",
		:options="{group:'col', forceFallback: true, animation: 150, scrollSpeed: 0, scrollSensitivity:0}", 
		@end="drag=false"
	)
		.col-board(v-for="col in currentCols", :key="col.id")
			.header-col
				h3.title {{col.title}}
				button.button-more
			
			draggable(
				class="cards",
				v-model="col.tasks",
				@start="start",
				:options="{group:'cards', forceFallback: true, animation: 150, scrollSpeed: 0, scrollSensitivity:0}",
				@end="end"
			)
				.card(v-for="(task, index) in col.tasks", :key="index", :ref="task.id" @click="openTask(task.id, col.id, $event.target)")
					.content-card
						.users
							.user(v-for="user in task.users", :class="user.master ? 'master' : ''")
								.photo(:style="{'background-image': 'url('+user.photo+')'}")
								span.name

						.content-text
							h3.title {{task.content.title}}

							.footer-content
								.date {{task.content.date}}
								.count-list {{task.content.list}}
								.count-comments {{task.content.comments}}
								.count-anexos {{task.content.anexos}}

			.footer
				.created-task
					we-editable(
						:content="contentTask.title",
						@update="contentTask.title = $event", 
						placeholder="Adicionar tarefa"
					)
					.buttons
						button.button-add Adicionar
						button.button-cancel Cancelar
</template>

<script>
	import WeEditable from '@/components/common/We-Editable.vue'
	import draggable from 'vuedraggable'
	export default {
		name: 'w-colboard',
		components:{
			'we-editable': WeEditable,
			'draggable': draggable
		},
		// props: ['data','idBoard','idCol','cols'],
		props: {
			cols: Array,
			idBoard: Number
		},
		data () {
			return {
				// a: this.idCol,
				currentIdCol: null,
				currentCols: this.cols,
				currentIdBoard: this.idBoard,
				contentTask: {
					title: null
				},
				msg: 'Welcome to Your Vue.js App'
			}
		},
		mounted(){
			// console.log(this.cols[0])
		},
		methods:{
			start(item){
				item.item.setAttribute('data-drag', true);
			},
			end(item){
				setTimeout(() => { 
					item.item.removeAttribute('data-drag');
				}, 10);
			},
			openTask(taskId, colId, item){
				let checkDrop = item.getAttribute('data-drag');
				if (!checkDrop) {
					this.$router.push({
					    name: 'task',
					    params: {
					        type: 'task',
					        idBoard: this.currentIdBoard,
					        idTask: taskId,
					        idCol: colId
					    }
					})
				}
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/boards/ColsBoard.styl"></style>


